st cliqué */\n}\n.search {\n  display: flex;\n  justify-content: space-between;\n}\n.search_icon {\n  max-height: 60px;\n  max-width: 60px;\n}\n.flex-container {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 1rem;\n  margin-top: 1rem;\n}\n.flex-item-category {\n  padding-right: 45%;\n  /* Espace entre les textes (ajustez selon vos besoins) */\n  font-weight: 700;\n  color: #515151;\n  font-size: 1.8rem;\n  margin-bottom: 1rem;\n  margin-top: 0.2rem;\n}\n.flex-item-add {\n  margin-right: 90px;\n  /* Espace entre les textes (ajustez selon vos besoins) */\n}\n.custom-icon {\n  font-size: 24px;\n  /* Custom size: 24 pixels */\n}\n.animated-arrow {\n  animation: colorChange 3s infinite;\n  /* Appliquer l'animation 'colorChange' avec une durée de 3 secondes en boucle */\n  font-size: 3rem;\n  /* Taille personnalisée pour l'icône */\n}\n@keyframes colorChange {\n  0% {\n    color: red;\n    /* Couleur au début de l'animation */\n  }\n  25% {\n    color: blue;\n    /* Couleur à 25% de l'animation */\n  }\n  50% {\n    color: green;\n    /* Couleur à 50% de l'animation */\n  }\n  75% {\n    color: orange;\n    /* Couleur à 75% de l'animation */\n  }\n  100% {\n    color: red;\n    /* Couleur à la fin de l'animation */\n  }\n}\n.versionPc {\n  margin: auto;\n  width: 500px;\n  height: 500px;\n}\n/*# sourceMappingURL=data:application/json;base64, */";